import React from 'react';
import './App.css';
import { ThemeProvider, createTheme, Arwes, Header, Button, Words, Footer, List, Image, Blockquote, Row, Col, SoundsProvider, createSounds, Table, Frame } from 'arwes';
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

const myTheme = {
  margin: '0 auto',
  fontFamily: 'Agency FB'
};

const mySounds = {
  shared: { volume: 1, },  // Shared sound settings
  players: {  // The player settings
    click: {  // With the name the player is created
      sound: { src: ['click.mp3'] }  // The settings to pass to Howler
    },
    typing: {
      sound: { src: ['typing.mp3'] },
      settings: { oneAtATime: true }  // The custom app settings
    },
    deploy: {
      sound: { src: ['/sound/deploy.mp3'] }, //WOULD HAVE TO BUY THESE?
      settings: { oneAtATime: true }
    },
  }
};

const App = () => (
  <ThemeProvider theme={createTheme(myTheme)}>
    <SoundsProvider sounds={createSounds(mySounds)}>
      <Arwes animate>
        <Router>
          <div style={{ padding: 50, fontFamily: 'Agency FB', textAlign: 'center'}}>
              <Header animate style={{ fontSize: 80 }}>
              <div style={{textAlign: 'center'}}>Carleton Cyber Security Club
              <img src="ccsclogo5.png"/></div>
              <nav>
                <div style={{ textAlign: 'center', width: '100%'}}>
                  <Link animate style={{ padding: 20}} to="/"><Words animate>Home</Words></Link>
                  <Link animate style={{ padding: 20}} to="/events"><Words animate>Events</Words></Link>
                  <Link animate style={{ padding: 20}} to="/hackers"><Words animate>Hackers</Words></Link>
                  <Link animate style={{ padding: 20}} to="/contract"><Words animate>Contract</Words></Link>
                  <Link animate style={{ padding: 20}} to="/join"><Words animate>Join</Words></Link>
                </div>
              </nav>
            </Header>
          </div>
          <div style={{ padding: 0, fontFamily: 'Agency FB', textAlign: 'center'}}>
          <Switch>
            <Route exact path="/">
              <Home />
            </Route>
            <Route exact path="/events">
              <Events />
            </Route>
            <Route exact path="/hackers">
              <Hackers />
            </Route>
            <Route exact path="/contract">
              <Contract />
            </Route>
            <Route exact path="/join">
              <Join />
            </Route>
          </Switch>
          </div>
          <div style={{ padding: 50, fontFamily: 'Agency FB', textAlign: 'center'}}>
            <Footer animate style={{fontSize: 14, textAlign: 'center'}}>
              <br></br> 
              © Carleton Cyber Security Club, 2020
            </Footer>
          </div>
        </Router>
      </Arwes>
      </SoundsProvider>
  </ThemeProvider>
);

export default App;

